//
// Buttons
// --------------------------------------------------

.btn {
  display: inline-block;
  color: $black;
  padding: 10px 20px;
  font-size: $body-font-size;
  font-weight: $font-weight-bold;
  font-family: $font-family-heading;
  cursor: pointer;
  line-height: 1;
  border-radius: $border-radius;
  appearance: none; // Corrects inability to style clickable `input` types in iOS.
  background-color: transparent;
  transition: background-color .1s ease 0s;
  border: 1px solid transparent;
  vertical-align: middle;
  text-align: center;
  user-select: none;

  [disabled],
  :disabled,
  &.disabled {
    opacity: .5;
  }

  & + & {
    margin-left: $spacer-2;
  }
}

.btn--large {
  padding: 20px 40px;
  font-size: 20px;
}

.btn--small {
  padding: 8px 16px;
  font-size: 14px;
}

.btn-primary { @include btn-solid(lighten($black, 20%), $yellow); }
.btn-secondary { @include btn-outline(lighten($black, 20%), $yellow); }
.btn-primary-danger { @include btn-solid($white, $red); }
.btn-secondary-danger { @include btn-outline($red, $red, $white); }
.btn-primary-light { @include btn-solid(lighten($black, 20%), $white); }
.btn-secondary-light { @include btn-outline($white, $white, lighten($black, 20%)); }



