//
// Form Control
// --------------------------------------------------

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

.form-control {
  background: $white;
  font-size: 1rem;
  line-height: 1.5rem;
  border: 1px solid rgba(0, 0, 0, .2);
  vertical-align: middle;
  outline: none;
  border-radius: 0;
}

textarea {
  width: 100%;
}

.form-select {
  display: inline-block;
  max-width: 100%;
  appearance: none;
  background-color: $white;
  background-image: url("data:image/svg+xml,%3Csvg%20xmlns%3D%27http%3A//www.w3.org/2000/svg%27%20width%3D%2712%27%20height%3D%278%27%20viewBox%3D%270%200%20488%20285%27%3E%3Cpath%20d%3D%27M483.11%2029.381l-24.449-24.485c-2.934-2.938-7.335-4.897-11.246-4.897-3.912%200-8.313%201.959-11.246%204.897l-192.168%20192.448-192.168-192.448c-2.934-2.938-7.335-4.897-11.246-4.897-4.401%200-8.313%201.959-11.246%204.897l-24.449%2024.485c-2.934%202.938-4.89%207.345-4.89%2011.263s1.956%208.325%204.89%2011.263l227.864%20228.196c2.934%202.938%207.335%204.897%2011.246%204.897%203.912%200%208.313-1.959%2011.246-4.897l227.864-228.196c2.934-2.938%204.89-7.345%204.89-11.263s-1.956-8.325-4.89-11.263z%27%20fill%3D%27%23666%27/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: calc(100% - 1rem) center;
  background-size: .6875rem;
  padding-right: 2.5rem;
  color: #666;
}

// ----------------------------------------

.input-sm {
  min-height: 28px;
  padding: 8px 16px;
  font-size: 14px;
  line-height: 20px;
}

.input-lg {
  padding: 20px 40px;
  font-size: 20px;
}

.input-block {
  display: block;
  width: 100%;
}

// --------------------------------------------------
// Form Control
// --------------------------------------------------

.form-checkbox {
  padding-left: 20px;
  margin: 15px 0;
  vertical-align: middle;

  label {
    .em,
    .highlight {
      position: relative;
      left: -$spacer-1;
      padding: 2px $spacer-1;
      font-style: normal;
      background: $yellow;
      border-radius: 3px;
    }
  }

  [type="checkbox"],
  [type="radio"] {
    float: left;
    margin: 5px 0 0 -20px;
    vertical-align: middle;
  }

  .note {
    display: block;
    margin: 0;
    font-size: $h6-font-size;
    font-weight: $font-weight-normal;
    color: $grey;
  }
}

.form-warning {
  padding: $spacer-2 10px;
  margin: 10px 0;
  font-size: $h5-font-size;
  color: $yellow;
  background: $yellow;
  border: 1px solid $border-color;
  border-radius: 3px;

  p {
    margin: 0;
    line-height: $lh-default;
  }

  a { font-weight: $font-weight-bold; }
}

// ------------------------------------------------
// Form Select
// --------------------------------------------------

.select-sm {
  height: 28px;
  min-height: 28px;
  padding-top: 3px;
  padding-bottom: 3px;
  font-size: $h6-font-size;

  &[multiple] {
    height: auto;
    min-height: 0;
  }
}

// --------------------------------
// Input Group
// --------------------------------

.input-group {
  display: table;

  .form-control {
    position: relative;
    width: 100%;

    &:focus {
      z-index: 2;
    }

    + .btn {
      margin-left: 0;
    }
  }

  // For when you want the input group to behave like inline-block.
  &.inline {
    display: inline-table;
  }
}

.input-group .form-control,
.input-group-button {
  display: table-cell;
}

.input-group-button {
  width: 1%;
  vertical-align: middle; // Match the inputs
}

.input-group .form-control:first-child,
.input-group-button:first-child .btn {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.input-group-button:first-child .btn {
  margin-right: -1px;
}

.input-group .form-control:last-child,
.input-group-button:last-child .btn {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.input-group-button:last-child .btn {
  margin-left: -1px;
}

.form-control + .btn {
  margin-left: .5rem;
}



